.clock {
  display: inline-block;
  border-radius: 100%;
  font-family: "Montserrat";
}

.clock.tiny {
  width: 75px;
  height: 75px;
  border-radius: 100%;
}

.clock.tiny .minute,
.clock.tiny .hour {
  height: 36px;
  width: 1.5px;
  top: -36px;
}

.clock.small {
  width: 100px;
  height: 100px;
  border-radius: 100%;
}

.clock.small .minute,
.clock.small .hour {
  height: 48px;
  width: 2px;
  top: -48px;
}

.clock.regular {
  width: 200px;
  height: 200px;
  border-radius: 100%;
}

.clock.regular .minute,
.clock.regular .hour {
  height: 96px;
  width: 4px;
  top: -96px;
}

.clock.large {
  width: 300px;
  height: 300px;
  border-radius: 100%;
}

.clock.large .minute,
.clock.large .hour {
  height: 144px;
  width: 6px;
  top: -144px;
}

.clock .wrap {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 2s linear;
  transition: opacity 2s linear;
}

.clock .hidden {
  visibility: hidden;
  opacity: 0;
}

.clock .minute,
.clock .hour {
  position: absolute;
  margin: auto;
  left: 0;
  bottom: 0;
  right: 0;
  background: #fff;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
  z-index: 1;
  -webkit-transition: -webkit-transform 0.8s ease-in-out;
  transition: -webkit-transform 0.8s ease-in-out;
  transition: transform 0.8s ease-in-out;
  transition: transform 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out;
}

.clock .second {
  position: absolute;
  height: 90px;
  width: 2px;
  margin: auto;
  top: -26%;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 4px;
  background: #ff4b3e;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  z-index: 1;
}

.clock .dot {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 8px;
  height: 8px;
  border-radius: 100px;
  background: #fff;
  border: 3px solid #ffffff;
  border-radius: 100px;
  margin: auto;
  z-index: 1;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
}

.divider {
  font-size: 6em;
  -webkit-animation-name: flash;
          animation-name: flash;
  -webkit-animation-duration: 2000ms;
          animation-duration: 2000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

@-webkit-keyframes flash {
  0% {
    color: white;
  }
  50% {
    color: transparent;
  }
  100% {
    color: white;
  }
}

@keyframes flash {
  0% {
    color: white;
  }
  50% {
    color: transparent;
  }
  100% {
    color: white;
  }
}
